import React, { useState } from "react"

const Index = () => {
  const [compass, setCompass] = useState(true)
  const [factions, setFactions] = useState(true)
  const [lanes, setLanes] = useState(false)
  const [capitals, setCapitals] = useState(true)
  const [notables, setNotables] = useState(false)
  const [regions, setRegions] = useState(false)
  return (
    <div className="bottom-layer">
      <div className="button-row">
        <button
          className={compass ? "selected" : ""}
          onClick={e => setCompass(!compass)}
        >
          Compass
        </button>
        <button
          className={factions ? "selected" : ""}
          onClick={e => setFactions(!factions)}
        >
          Factions
        </button>
        <button
          className={lanes ? "selected" : ""}
          onClick={e => setLanes(!lanes)}
        >
          Hyperlanes
        </button>
        <button
          className={capitals ? "selected" : ""}
          onClick={e => setCapitals(!capitals)}
        >
          Capitals
        </button>

        <button
          className={notables ? "selected" : ""}
          onClick={e => setNotables(!notables)}
        >
          Notable
        </button>
        <button
          className={regions ? "selected" : ""}
          onClick={e => setRegions(!regions)}
        >
          Regions
        </button>
      </div>
      <div className={`map-layer factions ${factions ? "" : "hidden"}`} />
      <div className={`map-layer compass ${compass ? "" : "hidden"}`} />
      <div className={`map-layer capitals ${capitals ? "" : "hidden"}`} />
      <div className={`map-layer lanes ${lanes ? "" : "hidden"}`} />
      <div className={`map-layer notables ${notables ? "" : "hidden"}`} />
      <div className={`map-layer regions ${regions ? "" : "hidden"}`} />
    </div>
  )
}

export default Index
